import React from "react";
import { graphql } from "gatsby";
import {
  ContentfulRichTextGatsbyReference,
  RenderRichTextData,
} from "gatsby-source-contentful/rich-text";
import { FluidObject } from "gatsby-image";
import "./index.scss";
import { Layout } from "../components/Layout";
import { BackgroundBlock, AuthorBlock, ImageBlock, RenderRichContent, TimelineBlock } from "../components";
import "../styles/reset.css";
import "../styles/styles.scss";
import "./about-us.scss";
type AboutProps = {
  data: {
    allContentfulPageAboutUs: {
      nodes: {
        aboutUsFaces: {
          authorJobTitle: string;
          authorMail: string;
          authorName: string;
          authorPictureAltText: string;
          authorpicture: { fluid: FluidObject };
          socialMediaLinkedin: string;
        }[]
        aboutUsHeaderFaces: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        aboutUsImage: { fluid: FluidObject };
        aboutUsIntro: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        aboutUsReadMoreFaces: string;
        aboutUsReadMoreFacesText: string;
        slug: string;
        title: string;
        ourStoryHeader: string;
        ourStoryIntro: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        ourStoryEntries: {
          ourStoryHeader: string;
          ourStoryText: RenderRichTextData<ContentfulRichTextGatsbyReference>;
          ourStoryMonthYear: string;
          ourStoryIcon: { fluid: FluidObject };
        }[]
        aboutUsPartner: {
          title: string;
          partnerHeaderImage: { fluid: FluidObject };
          partnerHeaderText: string;
          partnerShortIntro: RenderRichTextData<ContentfulRichTextGatsbyReference>;
        }[]
      }[]
    }
  }
};
const AboutPage = ({ data }: AboutProps) => {
  const qweryData = data.allContentfulPageAboutUs.nodes[0];
  console.log(qweryData);
  const aboutImgBlock = {
    title: qweryData.title,
    image: qweryData.aboutUsImage,
    description: qweryData.aboutUsIntro
  }
  const timeline = qweryData.ourStoryEntries.map(item => {
    return {
      year: item.ourStoryMonthYear,
      title: item.ourStoryHeader,
      description: item.ourStoryText,
      image: item.ourStoryIcon
    }
  });


  return (
    <Layout>
      <BackgroundBlock
        backgroundImage="radial-gradient(circle at top left, #ffffff 0%, #eaf1f9 63%, #d9e2ed 96%)"
        height="100vh"
        width="100%"
      />
      <div className="spacer"></div>
      <div className="container">
      <div className="spacer"></div>
        <ImageBlock align="right" className="about-img-block" data={aboutImgBlock} />
        <div className="spacer"></div>
        <div className="authors">
          <div className="title"><RenderRichContent content={qweryData.aboutUsHeaderFaces} /></div>
          <div className="author-wrapper">{qweryData.aboutUsFaces.map((author, index) => {
            const temp = {
              title: author.authorName,
              function: author.authorJobTitle,
              email: author.authorMail,
              image: author.authorpicture,
              imageAlt: author.authorPictureAltText,
              linkedIn: author.socialMediaLinkedin
            }
            return (<AuthorBlock key={index} data={temp} className="face-block-container" />);
          })}</div>
          <a href={qweryData.aboutUsReadMoreFaces}><button className="more-authors">{qweryData.aboutUsReadMoreFacesText}</button></a>
        </div>
      </div>
      <TimelineBlock data={timeline} />
      <div className="spacer"></div>
      <div className="partners">{qweryData.aboutUsPartner.map((partner, index) => {
        const temp = {
          title: partner.title,
          image: partner.partnerHeaderImage,
          description: partner.partnerShortIntro,
          subHeading: {
            title: partner.partnerHeaderText
          },
          button: { title: 'Read more', link: '/' }
        }
        return (<ImageBlock key={index} data={temp} align={index % 2 == 0 ? 'left' : 'right'} className="partner" />)
      })}
      </div>
      {/*
      <div className="spacer"></div>
      <ImageBlock align="right" content={Features[2]} />
      <div className="spacer"></div>
      <ServiceBlock heading={expectations.heading} services={Services} /> */}
    </Layout>
  );
};

export default AboutPage;
export const query = graphql`
  query {
    allContentfulPageAboutUs(filter: {node_locale: {eq: "nl"}}) {
        nodes {
          slug
          title
          aboutUsIntro {
            raw
          }
          aboutUsHeaderFaces {
            raw
          }
          aboutUsReadMoreFaces
          aboutUsReadMoreFacesText
          aboutUsFaces {
            authorName
            authorJobTitle
            authorMail
            authorpicture {
              fluid(maxWidth: 320) {
                ...GatsbyContentfulFluid
              }
            }
            authorPictureAltText
            socialMediaLinkedin
          }
          aboutUsImage {
            fluid(maxWidth: 480) {
              ...GatsbyContentfulFluid
            }
          }
          ourStoryHeader
          ourStoryIntro {
            raw
          }
          ourStoryEntries {
            ... on ContentfulContentBlockOurStoryHistoryTimeline {
              ourStoryHeader
              ourStoryMonthYear
              ourStoryText {
                raw
              }
            }
          }
          aboutUsPartner {
            title
            partnerHeaderImage {
              fluid(maxWidth: 480) {
                ...GatsbyContentfulFluid
              }
            }
            partnerHeaderText
            partnerShortIntro {
              raw
            }
          }
        }
      }
    }    
`;
